.buttons{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    align-items: center;
    justify-content: center;
}
.number{
    width: 24%;
    align-items: center;
    justify-content: center;
    display: flex;
    height: 50px;
    padding: 2px;
    border: 1px  solid black;
    background-color: whitesmoke;
    color:Black;
    font-size: 25px;
}
.number-btn{
    width: 24%;
    align-items: center;
    justify-content: center;
    display: flex;
    height: 50px;
    padding: 2px;
    border: 1px  solid black;
    background-color: red;
    color:Black;
    font-size: 30px;
}
.number-btns{
    width: 24%;
    align-items: center;
    justify-content: center;
    display: flex;
    height: 50px;
    padding: 2px;
    border: 1px  solid black;
    background-color:green;
    color:Black;
    font-size: 30px;
    font-weight: 700;
}

.number:focus{
    transform: scale(1);
    color: white;
    background-color: gray;
    border: none;
}

@media screen and (max-width:800px){
    .number-btn{
    width: 24%;
    align-items: center;
    justify-content: center;
    display: flex;
    height: 50px;
    padding: 2px;
    border: 1px  solid black;
    background-color: red;
    color:Black;
    font-size: 20px;
}
}