h1{
    align-items: center;
}

.calculator{
    display: flex;
    align-items: center;
    flex-direction: column;
    border: 1px solid white;
    padding: 20px;
    width: 500px;
    background-color: #282c34;
}

@media screen and (max-width:800px){
    .calculator{
    margin-top: -100px;
    display: flex;
    align-items: center;
    flex-direction: column;
    border: 1px solid white;
    padding: 20px;
    width: 100% ;
    background-color: #282c34;
}
}